import React from 'react'
import './Banner.css'
import singharat from '../singharat.png';
import { FaLine,FaFacebook,FaInstagram,FaTwitter } from 'react-icons/fa';
function Banner() {
    
    return (
        <div>
            <div className="Banner" data-speed="-5" id="indexsec">
                <div className="container">
                        <div className="Bannercon">
                                <div className="bleft">
                                    <h4>สวัสดีครับผมชื่อ</h4>
                                    <h1>สิงหราช บุญพิม</h1>
                                    <p>นักเรียนชั้นมัธยมศึกษาปีที่6</p>
                                    <h5>โรงเรียนกำแพง</h5>
                                    <div className="blef-footer">
                                        <a className="buttonviewwork">ดูงานของผม</a>
                                        <div class="contact">
                                            <ul>
                                                <li><a><FaLine /></a></li>
                                                <li><a><FaFacebook /></a></li>
                                                <li><a><FaInstagram /></a></li>
                                                <li><a><FaTwitter /></a></li>
                                            </ul>
                                         </div>
                                    </div>
                                </div>
                                {/* <div className="bright">
                                    <img src={singharat} className="people"></img>
                                </div> */}
                        </div>
                </div>
            </div>
        </div>
    )
}

export default Banner;
